import React from 'react'
import Item from'./ExpenseTitem' ;
import {MdDelete} from 'react-icons/md'
const ExpenseList = ({expenses, handlDeletItem,handlEdititem,clearItems}) => {
    return (
        <div>
            <ul className="list">
        { expenses.map((expense) => {
            return <Item key={ expense.id} expense= {expense}  handlDeletItem={handlDeletItem}
            handlEdititem={handlEdititem} />
        })}
            </ul>

            {
                (expenses.length > 0 && <button className='btn' onClick={clearItems}> 
                supprimer les dépenses
                <MdDelete className="btn-icon"/>
                </button>  )
                
            }
        </div>
    )
}

export default ExpenseList
