import React from 'react'
import { MdEdit,MdDelete} from 'react-icons/md'


const ExpenseTitem = ({expense,handlDeletItem,handlEdititem}) => {

    const {id,charge,amount} = expense;

    return (
        <li className="item">
            <div className="info">
                <span className="expense"> {charge}</span>
                <span className="amount"> {amount}</span>
            </div>
            <div>
            <button className="edit-btn" aria-label="edit button " onClick={()=>handlEdititem(id)}>
                    <MdEdit/> 

                </button>
                <button className="clear-btn" aria-label="clear button" onClick={()=>handlDeletItem(id)}>
                    <MdDelete/> 

                </button>


            </div>
        </li>
    )
}

export default ExpenseTitem

